body {
  margin: 0;
  padding: 0;
  color: #333;
  box-sizing: border-box;
  background-color: #c3d5dd;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container {
}

.toolbar {
  padding: 20px 30px;
  margin-bottom: 50px;
  position: relative;
  z-index: 10;
  height: 100vh;
}
.toolbar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("https://i.pinimg.com/originals/33/45/54/3345540687e1794e718884db727a85a6.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}
.social_icon {
  display: flex;
  justify-content: flex-end;
  font-size: 30px;
  margin: 20px 30px;
}
.social_icon a {
  color: #fff;
}
.social_icon a:hover {
  color: #c3d5dd;
}
.social_icon i {
  padding: 0 10px;
}
.bor {
  border-top: 4px solid #000;
  border-bottom: 2px solid #000;
}
.carousel-caption {
  background-color: #0000006e;
}
.social {
  text-align: center;
  position: relative;
}
.carousel {
  position: relative;
  z-index: 6;
}
.social .fab {
  font-size: 40px;
  padding: 20px 0;
  margin: 0 20px;
  width: 127px;
}
.img {
  width: 100%;
  /*top: -250px;*/
  z-index: 0;
  background-size: cover;
  background-position: center;
  position: fixed;
}
.bg-light {
  /*background: linear-gradient(to right, #d2d1dd 0%, #579fbf 100%);*/
  background: #000 !important;
}
.navbar {
  transition: all 0.7s ease;
}
.dropdown-menu {
  padding: 20px 30px;
  width: 600px;
  height: auto;
}
.dropdown-menu .col {
  padding: 15px;
}
